import {YMaps, Map, Placemark, ZoomControl} from '@pbe/react-yandex-maps';

const placemarkGeometry = [60.035852, 30.338440]; // Координаты метки
const mapState = {center: [60.035852, 30.338440], zoom: 17, controls: []};
const MyMapComponent = () => (
    <YMaps>
        <Map state={mapState} style={{width: "100wv", height: "100vh"}}>
            <Placemark geometry={placemarkGeometry}/>
            <ZoomControl options={{
                adjustMapMargin: true,
                position: {top: '10px', right: '10px'},
                size: 'small',
                visible: true
            }}/>

        </Map>
    </YMaps>
);

export default MyMapComponent;
