import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {VarietyWorkTypeList} from "../models/VarietyWorkTypeList";


export interface ListTypeWorksState {
    listVarietyWork:VarietyWorkTypeList[]
}


const initialState: ListTypeWorksState = {
    listVarietyWork: []

}

export const listVarietyWorksSlice = createSlice({
    name: 'listVarietyWork',
    initialState,
    reducers: {
        listVarietyWorkApp(state, action: PayloadAction<VarietyWorkTypeList[]>) {
            state.listVarietyWork = action.payload
        },


    }
})

export default listVarietyWorksSlice.reducer;