import React, {memo, ReactNode} from 'react';
import {TextField} from "@mui/material";
import {formAppSlice} from "../../../providers/slice/FormSlice";
import {useAppdispatch, useAppSelector} from "../../hooks/useRedux/redux";

interface InputYearCarProps {
    className?: string
    children?: ReactNode
    onClick: () => void
}


export const InputYearCar = memo((props: InputYearCarProps) => {

    const {formYearOfIssue} = formAppSlice.actions
    const {form} = useAppSelector(state => state.formAppSlice)

    const dispatch = useAppdispatch()


    const {
        className,
        children,
        onClick,
        ...otherProps
    } = props


    return (
        <div
            {...otherProps}
        >
            <TextField
                sx={{
                    '& .MuiTextField-root': {m: 1, width: '25ch'},
                    marginBottom: "10px"
                }}
                fullWidth
                size="small"
                error={
                (form?.yearOfIssue.length < 4 && form.varietyWork !== "")
            }
                helperText={
                (form?.yearOfIssue.length < 4 && form.varietyWork !== "" ? "Введите год в формате 1900" : "")
            }
                id="outlined"
                label={"Год выпуска"}
                type={"tel"}
                inputProps={{
                    maxLength: 4 // Ограничить ввод четырьмя символами
                }}
                value={form.yearOfIssue !== '0000' ? form.yearOfIssue : undefined}
                onClick={onClick}
                onChange={(e) =>
                    dispatch(formYearOfIssue(e.target.value))
                }
            />

        </div>
    );
});