import React, {memo, ReactNode, useEffect, useState} from 'react';
import {Box, Button, Slide, TextField, Typography} from "@mui/material";
import moment from "moment/moment";
import cls from "./Form.module.scss";
import DateScrollPicker from "../SelectDate/SelectDate";
import {useTimeIntervals} from "../../hooks/useTimeIntervals/useTimeIntervals";
import {listTypeWorkAndOwnerCarsSlice} from "../../../providers/slice/ListTypeWorkAndOwnerCarsSlice";
import {useAppdispatch, useAppSelector} from "../../hooks/useRedux/redux";
import {listVarietyWorksSlice} from "../../../providers/slice/ListVarietyWorkSlice";
import {varietyWorkSlice} from "../../../providers/slice/VarietyWorkSlice";
import {InputBrandCar} from "../InputBrandCar/InputBrandCar";
import {InputModelCar} from "../InputModelCar/InputModelCar";
import {InputYearCar} from "../InputYearCar/InputYearCar";
import {InputGosNumberCar} from "../InputGosNumberCar/InputGosNumberCar";
import {SelectTypeService} from "../SelectTypeService/SelectTypeService";
import {SelectVarietyService} from "../SelectVarietyService/SelectVarietyService";
import {formAppSlice} from "../../../providers/slice/FormSlice";
import {VarietyWorkTypeList} from "../../../providers/models/VarietyWorkTypeList";
import {SelectCar} from "../SelectCar/SelectCar";
import {ReceptionTabs} from "../ReceptionTabs/ReceptionTabs";

interface FormProps {
    className?: string
    children?: ReactNode
}

interface ApointmentData {
    // username: string;
    startDate: Date;
    endDate: Date;
    numberPhone: any;
    brandCar: string;
    modelCar: string;
    yearOfIssue: string;
    gosNumber: string;
    service: string;
    varietyWork: string
};


export const Form = memo((props: FormProps) => {
    const {
        className,
        children,
        ...otherProps
    } = props

    let tg = (window as any).Telegram.WebApp
    let color = tg?.colorScheme

    const {timeOptions, trigerInterval} = useTimeIntervals();
    const [sendButton, setSendButton] = useState(false)
    const [visibleSelectDate, setVisibleSelectDate] = useState(false)
    const [isPushedButton, setIsPushedButton] = useState("")
    const {varietyWorkCost} = varietyWorkSlice.actions
    const {varietyWork} = useAppSelector(state => state.varietyWorksSlice)
    const {slotTime} = useAppSelector(state => state.slotTimeSlice)
    const {form} = useAppSelector(state => state.formAppSlice)
    const {formStartDate} = formAppSlice.actions
    const {formCost} = formAppSlice.actions
    const {formRequestReception} = formAppSlice.actions
    const {formEndDate} = formAppSlice.actions
    const [duration_in_minutes, setDuration_in_minutes] = useState<number>(0)
    const {listTypeWorkAndOwnerCars} = useAppSelector(state => state.listTypeWorksSlice)


    const dispatch = useAppdispatch()
    const [tab, setTab] = useState(0);

    const handleChangeTabs = (event: React.SyntheticEvent, newValue: number) => {
        setTab(newValue);
    };


    useEffect(() => {
        if (
            isPushedButton === "" ||
            form?.brandCar.trim().length === 0 ||
            form?.modelCar.trim().length === 0 ||
            form?.yearOfIssue.trim().length < 4 ||
            form?.yearOfIssue === "0000" ||
            form?.gosNumber.trim().length === 0 ||
            form?.service.trim().length < 4 ||
            form?.service === "Автоэлектрика" ||
            form?.varietyWork.trim().length < 4 ||
            (form?.startDate.getHours() > 20 || (form?.startDate.getHours() === 20 && form?.startDate.getMinutes() > 0))
        ) {
            setSendButton(false)
        } else {
            setSendButton(true)

        }
        if (form.service === "Автоэлектрика") {
            tg.HapticFeedback.impactOccurred("rigid")


            const params = {
                title: "Внимание",
                message: "К автоэлектрику можно записаться только через чат или по телефону.",
                buttons: [
                    {"type": "default", "text": "Написать в чат", "id": "чат"},
                    {"type": "default", "text": "Позвонить", "id": "позвонить"}
                ]
            }
            tg.showPopup(params)


        }
    }, [form]);

    // TODO изменить тип даты
    const handleTimeChange = (time: Date) => {
        dispatch(formStartDate(time))
    };
    const handleChangeTime = (time: string) => {

        let [hours, minutes] = time.split(":").map(Number);
        const newStartDate = new Date(form.startDate); // Создание нового объекта Date на основе существующей даты
        newStartDate.setHours(hours, minutes); // Установка новых часов и минут

        //TODO прикрутить получение интервала времени работы
        const newEndDate = new Date(form.startDate); // Создание нового объекта Date на основе существующей даты
        newEndDate.setHours(hours, minutes + duration_in_minutes); // Установка новых часов и минут


        dispatch(formStartDate(newStartDate))
        dispatch(formEndDate(newEndDate))
        setIsPushedButton(time)

    }
    const hapticAlert = () => {
        const messageAlert = "Данные отправлены"
        tg.HapticFeedback.impactOccurred("light")
        // tg.showAlert(messageAlert, [])
        // tg.sendData(apointmentData)
        tg.sendData(JSON.stringify(form))
    }

    const handleChangeVariety = (i: VarietyWorkTypeList) => {
        setDuration_in_minutes(i.duration_in_minutes)
        dispatch(varietyWorkCost(Number(i.cost)))
        dispatch(formCost(i.cost))
        trigerInterval(slotTime, form.startDate, i.duration_in_minutes)

    }


    const onSelect = (curentDate: Date) => {
        setVisibleSelectDate(false)
        trigerInterval(slotTime, curentDate, duration_in_minutes)


    }
    const sendAdminRequest = () => {
        tg.HapticFeedback.impactOccurred("rigid")
        dispatch(formRequestReception(true))


        const params = {
            title: "Внимание",
            message: "На эту дату свободного времени не осталось, но вы можете получить уведомление, если время освободится.",
            buttons: [
                {"type": "default", "text": "Получить уведомление", "id": "Получить"},
                {"type": "default", "text": "Выберу другую дату", "id": "Отказ"}
            ]
        }
        tg.showPopup(params)
    }
    tg.onEvent('popupClosed', function (e: any) {

        if (e.button_id === "Получить" && form.requestReception) {
            tg.sendData(JSON.stringify(form));
        } else if (e.button_id === "Отказ" && form.requestReception) {
            dispatch(formRequestReception(false));
        } else if (e.button_id === "чат") {
            tg.openTelegramLink("https://t.me/RsAutoPro");
        } else if (e.button_id === "позвонить") {
            window.open('tel:+79219396401');
        }


    });
    useEffect(() => {
        console.log(timeOptions)
        if (timeOptions.length === 0 && form.varietyWork !== "") {

            sendAdminRequest()
        }
    }, [timeOptions]);

    return (
        <Box
            sx={{bgcolor: 'background.paper'}}
        >
            <div className={cls.Form}
            >


                {
                    listTypeWorkAndOwnerCars.cars.length > 0 &&
                    <ReceptionTabs tab={tab} handleChangeTabs={handleChangeTabs}/>

                }


                <div className={cls.Form}>
                    {tab === 1 && <SelectCar/>}
                    {tab === 0 && <InputBrandCar onClick={() => setVisibleSelectDate(false)}/>}
                    {tab === 0 && <InputModelCar onClick={() => setVisibleSelectDate(false)}/>}
                    {tab === 0 && <InputYearCar onClick={() => setVisibleSelectDate(false)}/>}
                    {tab === 0 && <InputGosNumberCar onClick={() => setVisibleSelectDate(false)}/>}
                    <SelectTypeService/>

                    <SelectVarietyService onClick={handleChangeVariety} trigerInterval={trigerInterval}/>
                    <Slide
                        direction={"right"}

                        in={form.varietyWork !== ""} mountOnEnter unmountOnExit
                    >
                        <TextField
                            sx={{
                                '& .MuiTextField-root': {m: 1, width: '25ch'},
                                marginBottom: "10px"
                            }}
                            error={false}
                            fullWidth
                            size="small"
                            helperText=""
                            id="outlined"
                            label="Дата приема"
                            value={
                                `${moment(form.startDate).format("DD/MM/YYYY")}`
                            }
                            onClick={() => setVisibleSelectDate(true)}
                            inputProps={{readOnly: true}}
                        />

                    </Slide>


                </div>
                <div className={cls.slide}>
                    <Slide
                        direction={"right"}

                        in={sendButton} mountOnEnter unmountOnExit>
                        <Button
                            onClick={hapticAlert}
                            sx={{margin: "10px auto 10px auto"}}
                            variant="contained"
                            size="large"
                            disableElevation
                        >
                            Отправить
                        </Button>


                    </Slide>

                </div>

                <div className={cls.groupBattonTime}>
                    {form?.service !== "Автоэлектрика" && timeOptions && timeOptions.map((item, index) => (
                        <Button
                            className={!visibleSelectDate && timeOptions.length !== 0 ? `${cls.selectTime} ${cls.openSelect}` : `${cls.selectTime} ${cls.closeSelect}`}
                            sx={{margin: "10px", height: "40px"}}
                            variant={isPushedButton === item ? "outlined" : "contained"}
                            size="small"
                            // disableElevation
                            onClick={() => handleChangeTime(item)}
                            key={index}
                        >
                            {item}
                        </Button>
                    ))}
                </div>

                <div
                    className={visibleSelectDate ? `${cls.selectDate} ${cls.openSelect}` : `${cls.selectDate} ${cls.closeSelect}`}
                >
                    <DateScrollPicker onSelect={onSelect} onTimeChange={handleTimeChange}/>
                </div>


            </div>
        </Box>
    );
});