import React, {memo, ReactNode, useState} from 'react';
import {FormControl, InputLabel, MenuItem, Select, Typography} from "@mui/material";
import {useAppdispatch, useAppSelector} from "../../hooks/useRedux/redux";
import {varietyWorkSlice} from "../../../providers/slice/VarietyWorkSlice";
import {formAppSlice} from "../../../providers/slice/FormSlice";
import cls from "./SelectVarietyService.module.scss"
import {IntervalDate} from "../../../providers/models/IntervalDate";
import {VarietyWorkTypeList} from "../../../providers/models/VarietyWorkTypeList";

interface SelectVarietyServiceProps {
    className?: string
    children?: ReactNode
    trigerInterval: (events: IntervalDate[], curentDate: Date, intervalInMinutes: number) => void
    onClick: (i: VarietyWorkTypeList) => void
}


export const SelectVarietyService = memo((props: SelectVarietyServiceProps) => {

    const [duration_in_minutes, setDuration_in_minutes] = useState<number>(0)
    const {listVarietyWork} = useAppSelector(state => state.listVarietyWorksSlice)
    const {varietyWorkCost} = varietyWorkSlice.actions
    const {varietyWork} = useAppSelector(state => state.varietyWorksSlice)
    const {slotTime} = useAppSelector(state => state.slotTimeSlice)
    const {formVarietyWork} = formAppSlice.actions
    const {form} = useAppSelector(state => state.formAppSlice)
    const dispatch = useAppdispatch()
    // const {timeOptions, trigerInterval} = useTimeIntervals();
    let tg = (window as any).Telegram.WebApp
    let color = tg?.colorScheme

    const {
        className,
        children,
        trigerInterval,
        onClick,
        ...otherProps
    } = props

    // useEffect(() => {
    //     slotTime && trigerInterval(slotTime, form.startDate, duration_in_minutes)
    // }, [form.startDate,slotTime]);

    return (
        <div
            className={cls.SelectVarietyService}
        >
            <FormControl
                sx={{
                    '& .MuiTextField-root': {m: 1, width: '25ch'},
                    marginBottom: "10px",
                }}

                fullWidth size="small">
                <InputLabel id="demo-select-small-label">Определить стоимость</InputLabel>
                <Select
                    labelId="demo-select-small-label"
                    id="demo-select-small"
                    value={form.varietyWork}
                    label="Определить стоимость"
                    onChange={(e) =>
                        dispatch(formVarietyWork(e.target.value))

                    }

                >
                    {listVarietyWork && listVarietyWork.map((item, index) => (
                        <MenuItem
                            onClick={() => {
                            onClick(item)}
                        }
                                  key={index}
                                  value={item.name_variety}>{item.name_variety}
                        </MenuItem>
                    ))}

                </Select>

            </FormControl>
            <Typography sx={{color: color === 'dark' ? 'white' : 'black'}} className={cls.cost}
                        variant="body1">
                От {varietyWork.cost}
            </Typography>

        </div>
    );
});