import React, {ChangeEvent, memo, ReactNode} from 'react';
import {TextField} from "@mui/material";
import {formAppSlice} from "../../../providers/slice/FormSlice";
import {useAppdispatch, useAppSelector} from "../../hooks/useRedux/redux";

interface InputGosNumberCarProps {
    className?: string
    children?: ReactNode
    onClick: () => void
}


export const InputGosNumberCar = memo((props: InputGosNumberCarProps) => {

    const {formGosNumber} = formAppSlice.actions
    const {form} = useAppSelector(state => state.formAppSlice)
    const dispatch = useAppdispatch()
    // const handleGosNumberChange = useCustomGosNumberHook(dispatch(formGosNumber()));

    const handleGosNumberChange = (e: ChangeEvent<HTMLInputElement>) => {
        let formattedGosNumberChange = e.target.value;
        const preLastChar = formattedGosNumberChange[formattedGosNumberChange.length - 2];
        const lastChar = formattedGosNumberChange[formattedGosNumberChange.length - 1];

        if (formattedGosNumberChange.length > 1) {
            if (
                (!isNaN(parseInt(preLastChar)) && isNaN(parseInt(lastChar)) && preLastChar !== " " && lastChar !== " ")
                ||
                (!isNaN(parseInt(lastChar)) && isNaN(parseInt(preLastChar)) && lastChar !== " ")
            ) {
                formattedGosNumberChange = formattedGosNumberChange.slice(0, formattedGosNumberChange.length - 1) + " " + lastChar;
            }
        }

        dispatch(formGosNumber(formattedGosNumberChange.toUpperCase()))
    };


    const {
        className,
        children,
        onClick,
        ...otherProps
    } = props


    return (
        <div
            {...otherProps}
        >
            <TextField
                sx={{
                    '& .MuiTextField-root': {m: 1, width: '25ch'},
                    marginBottom: "10px"
                }}
                // error={apointmentData?.yearOfIssue.length < 4}
                fullWidth
                size="small"
                // helperText={apointmentData?.gosNumber.length < 4 ? "Введите гос номер в формате A000AA00" : ""}
                id="outlined"
                error={form?.gosNumber.length === 0 && form.varietyWork !== ""}
                helperText={form?.gosNumber.length === 0 && form.varietyWork !== "" ? "Поле не заполнено" : ""}
                label="Гос номер"
                onClick={onClick}
                onChange={handleGosNumberChange}
                value={form.gosNumber}
            />

        </div>
    );
});