import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {IntervalDate} from "../models/IntervalDate";


export interface SlotTimeState {
    slotTime:IntervalDate[]
}


const initialState: SlotTimeState = {
    slotTime: []

}

export const slotTimeSlice = createSlice({
    name: 'slotTimeApp',
    initialState,
    reducers: {
        slotTimeApp(state, action: PayloadAction<IntervalDate[]>) {
            state.slotTime = action.payload
        },


    }
})

export default slotTimeSlice.reducer;