import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {ReceptionsType} from "../models/ReceptionsType";


export interface ReceptionsState {
    receptions: ReceptionsType[]
}


const initialState: ReceptionsState = {
    receptions: []

}

export const receptionsSlice = createSlice({
    name: 'ReceptionsApp',
    initialState,
    reducers: {
        receptionsUserList(state, action: PayloadAction<ReceptionsType[]>) {
            state.receptions = action.payload
        },
    }
})

export default receptionsSlice.reducer;