import React, {memo, ReactNode} from 'react';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { SvgIcon } from '@mui/material';
import { keyframes } from '@emotion/react';
import { styled } from '@mui/system';

// Создаем анимацию градиента
const gradientAnimation = keyframes`
  0% {
    stop-color: #020267;
  }
  50% {
    stop-color: #41a4d9;
  }
  100% {
    stop-color: #020267;
  }
`;

// Создаем стилизованный компонент SvgIcon
const AnimatedIcon = styled(SvgIcon)`
  font-size: 35px;
  width: 1em;
  height: 1em;
  & .gradient-stop-1 {
    animation: ${gradientAnimation} 3s infinite;
  }
  & .gradient-stop-2 {
    animation: ${gradientAnimation} 3s infinite;
    animation-delay: 1.5s;
  }
`;

interface GradientIconProps {
    className?: string
    children?: ReactNode
}


export const GradientIcon = memo((props: GradientIconProps) => {
  const {
    className,
    children,
    ...otherProps
  } = props

  return (
      <AnimatedIcon viewBox="0 0 24 24">
        <defs>
          <linearGradient id="grad1" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" className="gradient-stop-1"/>
            <stop offset="100%" className="gradient-stop-2"/>
          </linearGradient>
        </defs>
          {children}
        {/*<CheckCircleIcon sx={{fill: 'url(#grad1)', fontSize: 'inherit'}}/>*/}
      </AnimatedIcon>)
});

