import React, {memo, ReactNode, useEffect} from 'react';
import {FormControl, InputLabel, MenuItem, Select} from "@mui/material";
import {formAppSlice} from "../../../providers/slice/FormSlice";
import {useAppdispatch, useAppSelector} from "../../hooks/useRedux/redux";
import {listVarietyWorksSlice} from "../../../providers/slice/ListVarietyWorkSlice";

interface SelectTypeServiceProps {
    className?: string
    children?: ReactNode
}


export const SelectTypeService = memo((props: SelectTypeServiceProps) => {

    const {formService} = formAppSlice.actions
    const {form} = useAppSelector(state => state.formAppSlice)
    const {listTypeWorkAndOwnerCars} = useAppSelector(state => state.listTypeWorksSlice)
    const {listVarietyWorkApp} = listVarietyWorksSlice.actions


    const dispatch = useAppdispatch()


    const {
        className,
        children,
        ...otherProps
    } = props

    useEffect(() => {
        if (form.service && listTypeWorkAndOwnerCars) {
            const selectedItem = listTypeWorkAndOwnerCars.type_of_work.find(item => item.name_type === form.service);
            if (selectedItem) {
                dispatch(listVarietyWorkApp(selectedItem.type_work));
            }
        }
    }, [form.service, listTypeWorkAndOwnerCars, dispatch]);



    return (
        <div
            {...otherProps}
        >
            <FormControl
                sx={{
                    '& .MuiTextField-root': {m: 1, width: '25ch'},
                    marginBottom: "10px"
                }}

                fullWidth size="small">
                <InputLabel id="demo-select-small-label">Выбор услуги</InputLabel>
                <Select
                    labelId="demo-select-small-label"
                    id="demo-select-small"
                    value={form.service}
                    label="Выбор услуги"
                    onChange={(e) =>
                        dispatch(formService(e.target.value))
                    }

                >
                    {listTypeWorkAndOwnerCars && listTypeWorkAndOwnerCars.type_of_work.map((item, index) => (
                        <MenuItem
                            key={index}
                            value={item.name_type}
                            // onClick={() => trigerInterval(TEST_TIME, item[1])}
                            onClick={() => {
                                dispatch(listVarietyWorkApp(item.type_work))
                            }}

                        >
                            {item.name_type}
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>

        </div>
    );
});