import React, {memo, ReactNode, useState} from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import {formAppSlice} from "../../../providers/slice/FormSlice";
import {useAppdispatch} from "../../hooks/useRedux/redux";


interface ReceptionTabsProps {
    className?: string
    children?: ReactNode
    tab: number
    handleChangeTabs: (event: React.SyntheticEvent, newValue: number) => void
}


export const ReceptionTabs = memo((props: ReceptionTabsProps) => {
    const {
        className,
        children,
        tab,
        handleChangeTabs,
        ...otherProps
    } = props

    const {ressetForm} = formAppSlice.actions
    const dispatch = useAppdispatch()


    return (
        <Box sx={{
            bgcolor: 'background.paper',
            paddingTop: "10px",
            paddingBottom: "20px",
            justifyContent: "center", display: "flex"

        }}>
            <Tabs
                value={tab}
                onChange={handleChangeTabs}
                variant="scrollable"
                scrollButtons={false}
                aria-label="scrollable prevent tabs example"
            >
                <Tab
                    onClick={()=>{dispatch(ressetForm())}}
                    sx={{
                    color: "rgb(2 123 252)",
                    fontFamily: "'RAYDIS', serif;",
                    fontSize: "0.6rem"

                }} label="Запись нового авто"/>
                <Tab sx={{
                    color: "rgb(2 123 252)",
                    fontFamily: "'RAYDIS', serif;",
                    fontSize: "0.6rem"
                }} label="Запись сохраненного авто"/>

            </Tabs>
        </Box>
    );
});