import {memo, ReactNode} from 'react';

interface HapticAlertProps {
    className?: string
    children?: ReactNode
    messageAlert?: string
}


export const HapticAlert = memo((props: HapticAlertProps) => {
    const {
        className,
        children,
        messageAlert,
        ...otherProps
    } = props

    let tg = (window as any).Telegram.WebApp

    const hapticAlert = ()=>{
        tg.HapticFeedback.impactOccurred("light")
        if(messageAlert){
            tg.showAlert(messageAlert, [])
        }
    }


    return (
        <div
            onClick={hapticAlert}
        >
            {children}
        </div>
    );
});