import React, {memo, ReactNode} from 'react';
import {TextField} from "@mui/material";
import {formAppSlice} from "../../../providers/slice/FormSlice";
import {useAppdispatch, useAppSelector} from "../../hooks/useRedux/redux";

interface InputBrandCarProps {
    className?: string
    children?: ReactNode
    onClick: () => void

}


export const InputBrandCar = memo((props: InputBrandCarProps) => {

    const {formBrandCar} = formAppSlice.actions
    const {form} = useAppSelector(state => state.formAppSlice)

    const dispatch = useAppdispatch()


    const {
        className,
        children,
        onClick,
        ...otherProps
    } = props


    return (
        <div
            {...otherProps}
        >
            <TextField
                sx={{
                    '& .MuiTextField-root': {m: 1, width: '25ch'},
                    marginBottom: "10px"
                }}
                fullWidth
                size="small"
                error={form?.brandCar.length === 0 && form.varietyWork !== ""}
                helperText={form?.brandCar.length === 0 && form.varietyWork !== "" ? "Поле не заполнено" : ""}
                id="outlined"
                label={"Марка"}
                onClick={onClick}
                value={form.brandCar}
                onChange={(e) =>
                    dispatch(formBrandCar(e.target.value))
                }
            />

        </div>
    );
});