import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {FormType} from "../models/FormType";


export interface FormState {
    form: FormType
}


const initialState: FormState = {
    form: {
        startDate: new Date(),
        endDate: new Date(),
        numberPhone: "+7 (900) 000 - 00 - 00",
        brandCar: "",
        modelCar: "",
        yearOfIssue: "0000",
        gosNumber: "",
        service: "",
        varietyWork: "",
        cost: 500,
        id_client: "",
        requestReception: false,
    }

}

export const formAppSlice = createSlice({
    name: 'formApp',
    initialState,
    reducers: {
        form(state, action: PayloadAction<FormType>) {
            state.form = action.payload
        },
        ressetForm(state) {
            state.form.brandCar = initialState.form.brandCar
            state.form.modelCar = initialState.form.modelCar
            state.form.yearOfIssue = initialState.form.yearOfIssue
            state.form.gosNumber = initialState.form.gosNumber

        },
        formStartDate(state, action: PayloadAction<Date>) {
            state.form.startDate = action.payload
        },
        formEndDate(state, action: PayloadAction<Date>) {
            state.form.endDate = action.payload
        },
        formNumberPhone(state, action: PayloadAction<string>) {
            state.form.numberPhone = action.payload
        },
        formBrandCar(state, action: PayloadAction<string>) {
            state.form.brandCar = action.payload
        },
        formModelCar(state, action: PayloadAction<string>) {
            state.form.modelCar = action.payload
        },
        formYearOfIssue(state, action: PayloadAction<string>) {
            state.form.yearOfIssue = action.payload
        },
        formGosNumber(state, action: PayloadAction<string>) {
            state.form.gosNumber = action.payload
        },
        formService(state, action: PayloadAction<string>) {
            state.form.service = action.payload
        },
        formVarietyWork(state, action: PayloadAction<string>) {
            state.form.varietyWork = action.payload
        },
        formCost(state, action: PayloadAction<number>) {
            state.form.cost = action.payload
        },
        formIdClient(state, action: PayloadAction<string>) {
            state.form.id_client = action.payload
        },
        formRequestReception(state, action: PayloadAction<boolean>) {
            state.form.requestReception = action.payload
        },
    }
})

export default formAppSlice.reducer;