import {memo, ReactNode} from 'react';
import cls from "./Slider.module.scss"
import muffler from "../../icons/muffler.png";
import chassis from "../../icons/chassis.png";
import engine from "../../icons/engine.png";
import wheel from "../../icons/wheeel.png";
import electric from "../../icons/electric.png";
import garage_car from "../../icons/ garage_car.png";
import stop from "../../icons/stop.png";
import turbine from "../../icons/turbine.png";
import rail from "../../icons/rail.png";
import air_conditioner from "../../icons/air_conditioner.png";
import steering_wheel from "../../icons/steering_wheel.png";
import chip from "../../icons/chip.png";
import color from "../../icons/color.png";
import other from "../../icons/other.png";
import {formAppSlice} from "../../providers/slice/FormSlice";
import {useAppdispatch} from "../../shared/hooks/useRedux/redux";
import {listVarietyWorksSlice} from "../../providers/slice/ListVarietyWorkSlice";

interface SliderProps {
    className?: string
    children?: ReactNode
    data?: any
    onClick: ()=> void
}


export const Slider = memo((props: SliderProps) => {
    const {formService} = formAppSlice.actions
    const {listVarietyWorkApp} = listVarietyWorksSlice.actions
    const dispatch = useAppdispatch()


    const SELECT_TO = {
        "Ремонт глушителя": muffler,
        "Ремонт ходовой": chassis,
        "Ремонт двигателя": engine,
        "Шиномонтаж": wheel,
        "Автоэлектрика": electric,
        "ТО автомобиля": garage_car,
        "Ремонт тормозной системы": stop,
        "Ремонт турбины": turbine,
        "Ремонт рулевой рейки": rail,
        "Авто-кондиционер": air_conditioner,
        "Перетяжка руля": steering_wheel,
        "ЧИП Тюнинг": chip,
        "Антикор": color,
        "Другое": other
    }


    const {
        className,
        children,
        data,
        onClick,
        ...otherProps
    } = props


    return (
        <div
            className={cls.Slider}
        >
            <div className={cls.wrapperElement}>
                {Object.entries(SELECT_TO).map(([serviceName, icon], index) => (
                    <div
                        onClick={()=>{
                            onClick();
                            dispatch(formService(serviceName));
                    }}
                        className={cls.gradient}>
                        <p className={cls.textHeader}>
                            {serviceName}
                        </p>
                        <img className={cls.icon} src={icon}/>

                    </div>

                ))}

            </div>

        </div>
    );
});