import {combineReducers, configureStore} from "@reduxjs/toolkit";
import formAppSlice from "../../providers/slice/FormSlice";
import carAppSlice from "../../providers/slice/CarSlice";
import listTypeWorksSlice from "../../providers/slice/ListTypeWorkAndOwnerCarsSlice";
import listVarietyWorksSlice from "../../providers/slice/ListVarietyWorkSlice";
import varietyWorksSlice from "../../providers/slice/VarietyWorkSlice"
import slotTimeSlice from "../../providers/slice/SlotsTimeSlice";
import receptionsSlice from "../../providers/slice/ReceptionsSlice";



const rootReducer = combineReducers({

    carAppSlice,
    listVarietyWorksSlice,
    varietyWorksSlice,
    listTypeWorksSlice,
    slotTimeSlice,
    formAppSlice,
    receptionsSlice


})

export const setupStore =() => {
    return configureStore({
        reducer: rootReducer,
    })
}
export type RootState = ReturnType<typeof  rootReducer>
export type AppStore = ReturnType<typeof setupStore>
export type  AppDispatch = AppStore["dispatch"]
