import React, {memo, ReactNode, useEffect, useState} from 'react';
import useAxios from "../../hooks/useAxios/useAxios";
import {FormControl, InputLabel, MenuItem, Select, SelectChangeEvent} from "@mui/material";
import {SELECT_VARIABLE_WORK} from "../../../entites/SELECT_VARIABLE_WORK";
import {CarType} from "../../../providers/models/CarType";
import {useAppdispatch, useAppSelector} from "../../hooks/useRedux/redux";
import {formAppSlice} from "../../../providers/slice/FormSlice";

interface SelectCarProps {
    className?: string
    children?: ReactNode
}


export const SelectCar = memo((props: SelectCarProps) => {


    const dispatch = useAppdispatch()
    const {listTypeWorkAndOwnerCars} = useAppSelector(state => state.listTypeWorksSlice)
    const {formBrandCar} = formAppSlice.actions
    const {formModelCar} = formAppSlice.actions
    const {formYearOfIssue} = formAppSlice.actions
    const {formGosNumber} = formAppSlice.actions


    const {
        className,
        children,
        ...otherProps
    } = props

    const handleChange = (event: SelectChangeEvent) => {

        // Найдем соответствующий объект car
        const car = listTypeWorkAndOwnerCars.cars.find(item => item.gos_number === event.target.value);
        console.log(car,"car")


        // Дополнительно, вы можете диспатчить значение в Redux состояние
        if (car) {
            dispatch(formGosNumber(car.gos_number));
            dispatch(formModelCar(car.model));
            dispatch(formBrandCar(car.brand));
            dispatch(formYearOfIssue(String(car.year_of_issue)));

        }
    };


    return (
        <div>
            <FormControl
                sx={{
                    '& .MuiTextField-root': {m: 1, width: '25ch'},
                    marginBottom: "10px",
                }}

                fullWidth size="small">
                <InputLabel id="demo-select-small-label">Выберите свой автомобиль</InputLabel>
                <Select
                    labelId="demo-select-small-label"
                    id="demo-select-small"
                    // value={(e)=> dispatch(formGosNumber(e.value))}
                    label="Определить стоимость"
                    onChange={handleChange}

                >
                    {listTypeWorkAndOwnerCars.cars && listTypeWorkAndOwnerCars.cars.map((item, index) => (
                        <MenuItem key={index}
                                  value={item.gos_number}>{item.brand} {item.model} {item.gos_number}
                        </MenuItem>
                    ))}

                </Select>
            </FormControl>


        </div>
    );
});