import React, {memo, ReactNode, useState} from 'react';
import {Box} from "@mui/material";
import cls from "./MainPage.module.scss"
import {DiscountCard} from "../../widgets/DiscountCard/DiscountCard";
import {Widget} from "../../widgets/Widget/Widget";
import call from "../../icons/call.png";
import telegram from "../../icons/telegram.png";
import geo from "../../icons/geo.png";
import {Slider} from "../../widgets/Slider/Slider";
import {HapticAlert} from "../../shared/ui/HapticAlert/HapticAlert";
import {useParams} from "react-router-dom";


interface MainPageProps {
    className?: string
    children?: ReactNode
    isCard: () => void;
    openChat: () => void;
}


export const MainPage = memo((props: MainPageProps) => {

    let tg = (window as any).Telegram.WebApp



    const bg = tg.colorScheme
    tg.setBackgroundColor(bg === "light" ? "#e6e5ea" : "#000000")
    tg.setHeaderColor(bg === "light" ? "#e6e5ea" : "#000000")
    tg.expand()
    // tg.requestContact()
    const openPhone = () => {
        window.open('tel:+79219396401');
    }


    const {
        className,
        children,
        isCard,
        openChat,
        ...otherProps
    } = props


    return (
        <div

        >

            <DiscountCard/>
            <Box sx={{
                borderRadius: "10px",
                margin: "20px 0 50px 0",
                fontFamily: "'Roboto', 'Helvetica', 'Arial', 'sans-serif'",
                fontWeight: "600",
                lineHeight: "1.5",
                letterSpacing: "0.00938em",
                marginBottom: "0.35em",
                color: "rgb(2 123 252)",
                fontSize: "13px"
            }}>
                <div className={cls.header}>
                    {children}

                </div>
            </Box>

            <HapticAlert>
                <div onClick={openPhone}>
                    <Widget text={"Позвонить нам"} icon={call}/>
                </div>
            </HapticAlert>

            <HapticAlert>
                <div onClick={openChat}>
                    <Widget text={"Написать нам"} icon={telegram}/>
                </div>
            </HapticAlert>
            <HapticAlert>
                <div onClick={isCard}>
                    <Widget text={"Мы на карте"} icon={geo}/>
                </div>
            </HapticAlert>
        </div>
    );
});