import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {TypeWork} from "../models/TypeWork";
import {CarType} from "../models/CarType";
import {ListCarsAndTypeOfWork} from "../models/ListCarsAndTypeOfWork";


export interface ListTypeWorksState {
    listTypeWorkAndOwnerCars: ListCarsAndTypeOfWork
}


const initialState: ListTypeWorksState = {
    listTypeWorkAndOwnerCars: {
        "type_of_work": [],
        "client": {
            id: 0,
            phone: '',
            communication_method: '',
            amount_of_orders: '',
            bonus_card: 0,
            first_name: '',
            last_name: '',
            chat_id_telegram: 0,
            username: '',
            name_organization: 0
        },
        "cars": []

    }

}

export const listTypeWorkAndOwnerCarsSlice = createSlice({
    name: 'listTypeWorkAndOwnerCars',
    initialState,
    reducers: {
        listTypeWorkAndOwnerCarsApp(state, action: PayloadAction<ListCarsAndTypeOfWork>) {
            state.listTypeWorkAndOwnerCars = action.payload
        },


    }
})

export default listTypeWorkAndOwnerCarsSlice.reducer;