import {memo, ReactNode} from 'react';
import {CardReception} from "../CardReception/CardReception";
import {useAppSelector} from "../../hooks/useRedux/redux";
import * as React from "react";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import { parseISO, format } from 'date-fns';
import useAxios from "../../hooks/useAxios/useAxios";
import {ListCarsAndTypeOfWork} from "../../../providers/models/ListCarsAndTypeOfWork";

interface HistoryProps {
    className?: string
    children?: ReactNode
}


export const History = memo((props: HistoryProps) => {
    const {receptions} = useAppSelector(state => state.receptionsSlice)


    const {
        className,
        children,
        ...otherProps
    } = props


    return (
        <div>
            {receptions &&
                receptions.map((reception) => (
                    <CardReception key={reception.id}>
                        <React.Fragment>
                            <CardContent>
                                <Typography sx={{fontSize: 14}} color="text.secondary" gutterBottom>
                                    {format(reception.date_time_record_start, "dd/MM/yy - HH:mm")}
                                </Typography>
                                <Typography variant="h5" component="div">
                                    {reception.type_of_work}
                                </Typography>
                                <Typography sx={{mb: 1.5}} color="text.secondary">
                                    {`${reception.car.brand} ${reception.car.model} ${reception.car.year_of_issue} г.в.`}
                                </Typography>
                                <Typography variant="body2">
                                    ГОС НОМЕР: {reception.car.gos_number}
                                </Typography>
                            </CardContent>
                        </React.Fragment>
                    </CardReception>
                ))}
        </div>
    );
});