import {IntervalDate} from "../../../providers/models/IntervalDate";
import {useCallback, useEffect, useMemo, useState} from "react";

interface EventsDate {
    date_time_record_start: Date;
    date_time_record_end: Date;
}

// Функция для определения промежутков времени между событиями
export function useTimeIntervals() {


    const [timeOptions, setTimeOptions] = useState<string[]>([])

    const trigerInterval = useCallback((events: IntervalDate[], curentDate: Date, intervalInMinutes: number) => {
        // ... предыдущий код функции trigerInterval ...

        // получаем месяц, день и год из запрошенного дня
        const currentDate = curentDate.getDate()
        const currentMonth = curentDate.getMonth() + 1
        const currentYear = curentDate.getFullYear()


        const formatedEvents: IntervalDate[] = events.map(event => ({
            start: new Date(event.start),
            end: new Date(event.end)
        }));

        // добавляем границы времени работы организации
        const newEvents = [
            {
                start: new Date(`${currentYear}/${currentMonth}/${currentDate} 09:00`),
                end: new Date(`${currentYear}/${currentMonth}/${currentDate} 10:00`)
            },
            ...formatedEvents,

            {
                start: new Date(`${currentYear}/${currentMonth}/${currentDate} 21:00`),
                end: new Date(`${currentYear}/${currentMonth}/${currentDate} 22:00`)
            }]
        console.log(newEvents)

        // Список промежутков свободного времени
        const freeIntervals = [];

        // Проверяем если промежутки времени между записями
        for (let i = 1; i < newEvents.length; i++) {
            const prevEvent = newEvents[i - 1];
            const currentEvent = newEvents[i];

            // Если есть пересечение, добавляем промежуток времени
            if (prevEvent.end < currentEvent.start) {
                freeIntervals.push({
                    start: prevEvent.end,
                    end: currentEvent.start,
                });
            }
        }

        console.log(freeIntervals)


        // Нарезанные промежутки времени под конкретную услугу с учетом длительности услуги
        const intervals: string[] = [];

        // Возвращаем список промежутков времени
        freeIntervals.map((interval) => {
            const startMinutes = interval.start.getHours() * 60 + interval.start.getMinutes();
            const endMinutes = interval.end.getHours() * 60 + interval.end.getMinutes();
            const currentInterval = endMinutes - startMinutes

            // Начинаем нарезку интервала с учетом указанного интервала в минутах

            for (let i = startMinutes; i < endMinutes; i += intervalInMinutes) {
                const intervalStart = new Date(0, 0, 0, Math.floor(i / 60), i % 60); // Начало интервала
                if ((endMinutes - i) - intervalInMinutes >= 0) {
                    curentDate.getDate() === new Date().getDate() && curentDate.getHours() + 1 < intervalStart.getHours() &&
                    intervals.push(`${intervalStart.getHours().toString().padStart(2, '0')}:${intervalStart.getMinutes().toString().padStart(2, '0')}`);
                    // Проверка что это не текущий день
                    curentDate.getDate() !== new Date().getDate() &&
                    intervals.push(`${intervalStart.getHours().toString().padStart(2, '0')}:${intervalStart.getMinutes().toString().padStart(2, '0')}`);
                    // Форматируем время в строку HH:MM и добавляем в массив интервалов
                }
            }


        })
        setTimeOptions(intervals)

    }, []);

    const cachedTimeOptions = useMemo(() => {
        return timeOptions;
    }, [timeOptions]);


    return {timeOptions: cachedTimeOptions, trigerInterval};
}

export default useTimeIntervals
