import React, {memo, ReactNode, useEffect, useState} from 'react';
import {Form} from "../Form/Form";
import {MainPage} from "../../../pages/MainPage/MainPage";
import {HapticAlert} from "../HapticAlert/HapticAlert";
import cls from "../../../pages/MainPage/MainPage.module.scss";
import Card from "../../../widgets/Card/Card";
import {History} from "../History/History";
import useAxios from "../../hooks/useAxios/useAxios";
import {listTypeWorkAndOwnerCarsSlice} from "../../../providers/slice/ListTypeWorkAndOwnerCarsSlice";
import {useAppdispatch, useAppSelector} from "../../hooks/useRedux/redux";
import {ListCarsAndTypeOfWork} from "../../../providers/models/ListCarsAndTypeOfWork";
import {useParams} from "react-router-dom";
import {formAppSlice} from "../../../providers/slice/FormSlice";
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import PlaylistAddCheckCircleIcon from '@mui/icons-material/PlaylistAddCheckCircle';
import {GradientIcon} from "../GradientIcon/GradientIcon";
import {Box, CircularProgress, Typography} from "@mui/material";
import {receptionsSlice} from "../../../providers/slice/ReceptionsSlice";
import {ReceptionsType} from "../../../providers/models/ReceptionsType";
import {Slider} from "../../../widgets/Slider/Slider";

interface WrapperPageProps {
    className?: string
    children?: ReactNode
}


export const WrapperPage = memo((props: WrapperPageProps) => {
    const {
        className,
        children,
        ...otherProps
    } = props

    let tg = (window as any).Telegram.WebApp



    const [page, setPage] = useState("main")
    const [statusContact, setStatusContact] = useState<string>()
    const {data, error, loading, executeRequest} = useAxios<ListCarsAndTypeOfWork>();
    const {
        data: receptionsUser,
        error: errorReceptions,
        loading: loadingReceptions,
        executeRequest: requestReceptions
    } = useAxios<ReceptionsType[]>();
    const {listTypeWorkAndOwnerCarsApp} = listTypeWorkAndOwnerCarsSlice.actions
    const {formIdClient} = formAppSlice.actions
    const {receptionsUserList} = receptionsSlice.actions
    const {formNumberPhone} = formAppSlice.actions
    const {receptions} = useAppSelector(state => state.receptionsSlice)

    const dispatch = useAppdispatch()
    const {chat_id} = useParams()


    const backButton = () => {
        setPage("main")
    }
    const visibleButton = () => {
        tg.BackButton.show()
        tg.BackButton.onClick(backButton)

    }

    useEffect(() => {
        if (page === "main") {
            tg.BackButton.hide()
        } else {
            visibleButton()
        }
        if (chat_id) {
            dispatch(formIdClient(chat_id))
        }

    }, [page]);

    const fetcData = async () => {
        try {

            await executeRequest('GET', `server_app/get_type_of_work_variety/${chat_id}/`);


        } catch (error) {
            console.error('Ошибка получения данных:', error);
        }

    };
    const receptionsUserData = async () => {
        try {

            await requestReceptions('GET', `organization_app/get_history_reception/${chat_id}/`);


        } catch (error) {
            console.error('Ошибка получения данных:', error);
        }

    };
    useEffect(() => {
        if (receptionsUser && !loadingReceptions) {
            setPage("history")
            dispatch(receptionsUserList(receptionsUser))
        } else if (receptionsUser !== null && !loadingReceptions) {
            tg.showAlert("У вас пока нет записей.", [])

        }

    }, [receptionsUser, loadingReceptions]);

    useEffect(() => {
        if (data && !loading) {
            dispatch(listTypeWorkAndOwnerCarsApp(data))
            dispatch(formNumberPhone(data.client.phone))
        }
    }, [data, loading]);


    const openCard = () => {
        setPage("card")
    }

    const openForm = () => {

        tg.requestContact(function (status: boolean) {

            if (!status) {
                const messageAlert = "Необходимо отправить номер телефона для записи."
                tg.showAlert(messageAlert, [])
            } else {
                setPage("form")
                fetcData()
            }
        })
                // setPage("form")
                // fetcData()

    }
    const openHistory = () => {


        receptionsUserData()

    }

    const openChat = () => {
        tg.openTelegramLink("https://t.me/RsAutoPro")
    }

    const sendCommand = () => {
        const request = "request"
        tg.HapticFeedback.impactOccurred("light")
        // tg.showAlert(messageAlert, [])
        // tg.sendData(apointmentData)
        tg.sendData(JSON.stringify(request))
    }


    return (
        <Box sx={{fontWeight: "300"}}>
            {page === "form" && !loading && !error && <Form/>}
            {page === "form" && error && <Typography
                sx={{
                    fontSize: "18px",
                    fontFamily: "'RAYDIS', serif;",
                    color: "rgb(2 123 252)",
                    position: "fixed",
                    top: "50%",
                    left: "50%",
                    transform: "translate(-50%, -50%)",
                    textAlign: "center",
                    width: "65vw"
                }}>Сервер перегружен попробуйте зайти позже.</Typography>
            }
            {page === "form" && loading && !error &&
                <CircularProgress
                    sx={{position: "fixed", top: "50%", left: "50%", transform: "translate(-50%, -50%)"}}/>
            }
            {page === "main" &&
                <MainPage openChat={openChat} isCard={openCard}>
                    <div>
                        <div className={cls.groupButtons}>
                            <HapticAlert>
                                <div onClick={openForm} className={cls.button}>
                                    <GradientIcon>
                                        <AddCircleIcon sx={{fill: 'url(#grad1)', fontSize: 'inherit'}}/>
                                    </GradientIcon>
                                    <Typography sx={{fontSize: "11px", fontFamily: "'RAYDIS', serif;"}}>Записаться в автосервис</Typography>
                                </div>
                            </HapticAlert>
                            <div onClick={sendCommand} className={cls.button}>
                                <GradientIcon>
                                    <CheckCircleIcon sx={{fill: 'url(#grad1)', fontSize: 'inherit'}}/>
                                </GradientIcon>
                                <Typography sx={{fontSize: "11px", fontFamily: "'RAYDIS', serif;"}}>Активные
                                    записи</Typography>
                            </div>
                            <HapticAlert>
                                <div onClick={openHistory} className={cls.button}>
                                    <GradientIcon>
                                        <PlaylistAddCheckCircleIcon sx={{fill: 'url(#grad1)', fontSize: 'inherit'}}/>
                                    </GradientIcon>
                                    <Typography sx={{fontSize: "11px", fontFamily: "'RAYDIS', serif;"}}>История
                                        записей</Typography>
                                </div>
                            </HapticAlert>
                        </div>
                        <Slider onClick={openForm}/>

                    </div>
                </MainPage>
            }
            {page === "card" && <Card/>}
            {page === "history" && !loadingReceptions && <History/>}
            {page === "history" && !loadingReceptions && errorReceptions &&
                <Typography
                    sx={{
                        fontSize: "18px",
                        fontFamily: "'RAYDIS', serif;",
                        color: "rgb(2 123 252)",
                        position: "fixed",
                        top: "50%",
                        left: "50%",
                        transform: "translate(-50%, -50%)",
                        textAlign: "center",
                        width: "65vw"
                    }}>Сервер перегружен попробуйте зайти позже.</Typography>
            }
            {page === "history" && loadingReceptions &&
                <CircularProgress
                    sx={{position: "fixed", top: "50%", left: "50%", transform: "translate(-50%, -50%)"}}
                />
            }

        </Box>
    );
});