import React, {memo, ReactNode} from 'react';
import {TextField} from "@mui/material";
import {useAppdispatch, useAppSelector} from "../../hooks/useRedux/redux";
import {formAppSlice} from "../../../providers/slice/FormSlice";

interface InputModelCarProps {
    className?: string
    children?: ReactNode
    onClick: () => void
}


export const InputModelCar = memo((props: InputModelCarProps) => {

    const {formModelCar} = formAppSlice.actions
    const {form} = useAppSelector(state => state.formAppSlice)
    const dispatch = useAppdispatch()


    const {
        className,
        children,
        onClick,
        ...otherProps
    } = props


    return (
        <div
            {...otherProps}
        >
            <TextField
                sx={{
                    '& .MuiTextField-root': {m: 1, width: '25ch'},
                    marginBottom: "10px"
                }}
                fullWidth
                size="small"
                error={form?.modelCar.length === 0 && form.varietyWork !== ""}
                helperText={form?.modelCar.length === 0 && form.varietyWork !== "" ? "Поле не заполнено" : ""}
                id="outlined"
                label={"Модель"}
                onClick={onClick}
                value={form.modelCar}
                onChange={(e) =>
                    dispatch(formModelCar(e.target.value))
                }
            />

        </div>
    );
});