import React, {memo, useEffect, useState} from "react";
import DatePicker from "react-mobile-datepicker";
import useAxios from "../../hooks/useAxios/useAxios";
import {TypeWork} from "../../../providers/models/TypeWork";
import {varietyWorkSlice} from "../../../providers/slice/VarietyWorkSlice";
import {useAppdispatch, useAppSelector} from "../../hooks/useRedux/redux";
import {slotTimeSlice} from "../../../providers/slice/SlotsTimeSlice";
import {IntervalDate} from "../../../providers/models/IntervalDate";


interface DateScrollPickerProps {
    className?: string
    onTimeChange: (newTime: Date) => void
    onSelect: (newTime: Date) => void

}

const DateScrollPicker = memo((props: DateScrollPickerProps) => {
    let tg = (window as any).Telegram.WebApp
    const {
        onTimeChange,
        onSelect
    } = props
    const [time, setTime] = useState<Date>(new Date());
    const [isOpen, setIsOpen] = useState<boolean>(true);
    const {data, error, loading, executeRequest} = useAxios<IntervalDate[]>();
    const {slotTimeApp} = slotTimeSlice.actions
    const {slotTime} = useAppSelector(state => state.slotTimeSlice)

    const dispatch = useAppdispatch()
    const currentTime = new Date();


    const fetcData = async (day: number, month: number, year: number) => {
        try {


            await executeRequest('GET', `server_app/get_slots_reception/${day}/${month}/${year}/`);

        } catch (error) {
            console.error('Ошибка получения данных:', error);
        }
    };


    const handleClick = () => {
        setIsOpen(!isOpen);
    };
    useEffect(() => {
        handleClick()

    }, [time]);

    useEffect(() => {
        fetcData(time.getUTCDate(), time.getMonth() + 1, time.getFullYear())

    }, []);
    const haptic = (time: Date) => {



        // Проверяем, является ли выбранная дата временем в прошлом
        if (time < currentTime) {
            // Если выбранное время меньше текущего времени, устанавливаем текущую дату
            setTime(currentTime);
            onTimeChange(currentTime);

        } else {
            // Иначе используем выбранное время
            setTime(time);
            onTimeChange(time);
        }

        tg.HapticFeedback.selectionChanged()
        fetcData(time.getUTCDate(), time.getMonth() + 1, time.getFullYear())
    }
    const handleCancel = () => {
        setIsOpen(false);
        setTime(time)
        onTimeChange(time)
        tg.HapticFeedback.impactOccurred("light")
    };

    const handleSelect = (time: Date) => {
        setTime(time);
        onTimeChange(time);


        // const specifiedTime = new Date();
        // specifiedTime.setHours(20, 0, 0, 0); // Устанавливаем 20:00 как время сравнения
        //
        // if (time.getHours() > 20 || (time.getHours() === 20 && time.getMinutes() > 0)) {
        //     // Время больше 20:00
        //     const message = "Максимальное время записи 20:00"
        //     // tg.showAlert(message, [])
        //     console.log(message)
        //
        // } else {
        //     setTime(time);
        //     onTimeChange(time);
        //     fetcData(time.getUTCDate(), time.getMonth() + 1, time.getFullYear())
        //
        //
        // }
        //
        setIsOpen(false);
        onSelect(time);
    };
    useEffect(() => {
        if (data) {
            dispatch(slotTimeApp(data))
        }
    }, [data]);


    interface MonthData {
        [key: number]: string;
    }

    const monthMap: MonthData = {
        1: 'Январь',
        2: 'Февраль',
        3: 'Март',
        4: 'Апрель',
        5: 'Май',
        6: 'Июнь',
        7: 'Июль',
        8: 'Август',
        9: 'Сентябрь',
        10: 'Октябрь',
        11: 'Ноябрь',
        12: 'Декабрь',
    };

    const configDate = {
        'month': {
            format: (value: Date) => monthMap[value.getMonth() + 1],
            caption: 'Mon',
            step: 1,
        },
        'date': {
            format: 'DD',
            caption: 'Day',
            step: 1,
        },
        // 'hour': {
        //     format: 'hh',
        //     caption: 'Hour',
        //     step: 1,
        // },
        // 'minute': {
        //     format: 'mm',
        //     caption: 'Min',
        //     step: 1,
        // },
    };
    return (
        <div>
            {/*<button onClick={handleClick}>open</button>*/}
            <DatePicker

                value={time}
                isOpen={true}
                onSelect={handleSelect}
                onCancel={handleCancel}
                isPopup={false}
                showHeader={false}
                dateConfig={configDate}
                onChange={haptic}
                confirmText="Готово"
                cancelText="Сбросить"
                theme={"ios"}
            />

        </div>
    );
});
export default DateScrollPicker;
